.App {
  text-align: center;
}


nav{
  width: 100%;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  min-height: 10vh;
  background: rgb(47, 54, 56);
  color: #fff;
}
nav a{
  text-decoration: none;
}
nav a h3{
  color: #fff;
  font-size: 30px;
}

.nav-links{
  width: 30%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  list-style: none;
}
.nav-links a{
  text-decoration: none;
  color: #fff;
}
.course-section{
  width: 100%;
  height: 80vh;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
}
.course-section a{
  text-decoration: none;
  color: #fff;
  font-size:15px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items:flex-start;
}
.course-card{
  width: 200px;
  height: 200px;
  background-color: #61dafb;
  margin: 20px;
  box-shadow: 5px 5px 10px rgba(0, 0, 0,0.5);
}

.course-detail{
  width:90%;
  margin: 0 auto;
  height: 80vh;
}
.course-detail .row{
  display: flex;
  justify-content:space-between;
  align-items: center;
}

.course-detail .row .col-2 img{
  object-fit:cover;
  width: 400px;
  height: 400px;
  border: 3px solid #000;
  box-shadow: 5px 5px 10px rgba(0, 0, 0,0.5);
  margin-top: 40px;
}

.course-detail .row .col-1{
  display: flex;
  justify-content:center;
  align-items:flex-start;
  flex-direction: column;
}

.course-detail .row .col-1 h1{
    font-size: 35px;
}
.course-detail .row .col-1 p{
  font-size:20px;
}
.course-detail .row .col-1 a{
  text-decoration: none;
  background: #000;
  color: #fff;
  padding:10px 18px;
  border-radius: 10px;
}